<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">修改库位库区</h1>
           
          <label for="">所在仓库</label>
          <el-select v-model="task.warehouse_id" placeholder="请选择">
            <el-option
              v-for="item in warehouseOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>

          <label for="">所在库区</label>
          <el-select v-model="task.location" placeholder="请选择">
            <el-option
              v-for="item in locationOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
           
          <label for="">库位代码</label><el-input v-model="task.code" placeholder="请输入库位代码"></el-input>
          <label for="">备注</label><el-input v-model="task.memo" placeholder="备注"></el-input> 
           
        </el-col>
      </el-row>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addTask",
      data() {
          return {  
            task: {
              warehouse_id: "",
              location: "",
              code: "",
              memo: "" 
            },
           
            warehouseOptions: [
              {
                label: '国内仓',
                value: '1'
              },
              {
                label: '泰国仓',
                value: '2'
              } 
            ],
            locationOptions: [
              {
                label: 'A',
                value: 'A'
              },
              {
                label: 'B',
                value: 'B'
              },
              {
                label: 'C',
                value: 'C'
              },
              {
                label: 'D',
                value: 'D'
              },
              {
                label: 'E',
                value: 'E'
              },
              {
                label: 'F',
                value: 'F'
              },
              {
                label: 'G',
                value: 'G'
              }
            ]

          }
      },
      methods: {
        submitTask() {
          let url = "http://api.orkowms.me/sub-inventory";
          let params = {};
          let config = {};
          params.warehouse_id = this.task.warehouse_id;
          params.location = this.task.location;
          params.code = this.task.code;
          params.memo = this.task.memo;
           console.log('id:'+this.task.id)
          let that = this; 
          if(that.task.id){
             config = {
              headers : { 
              //  'Content-Type':'application/x-www-form-urlencoded'
              },
            };
             url = "http://api.orkowms.me/sub-inventory/"+this.task.id ; 
          } 

          this.$axios.put(url,params,config).then((res) => { 
              that.$message({
                type: 'success',
                message: '更新成功!'
              });
            
          }).catch((err) => {
             
              that.$message({
                type: 'error',
                message: '新增失败!'
              }); 
          })
        },
         
      },
      created() {
         let row = this.$route.query;  
          this.task.id = row.id; 
          this.task.warehouse_id = row.warehouse_id;
          this.task.location = row.location;
          this.task.code = row.code;
          this.task.memo = row.memo; 
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



